import React from 'react';
import Two from 'twojs-ts';
import PathFinder from '../projects/PathFinder';
import TwoCanvas from './TwoCanvas';
class ProjectCard extends React.Component {
    constructor(two) {
        super(two);
        this.state = {
            maximized: false,
            width: this.props.width,
            height: this.props.height,
            cssClass: "sim-div",
            gameInfoStyle: {display: "none"}
        }
    }
    maximize() {
        if (this.state.maximized)
            this.setState({
                maximized: false,
                width: 420,
                height: 260,
                style: {},
                gameInfoStyle: {display: "none"}
            });
        else{
            this.setState({
                maximized: true,
                width: 840,
                height: 520,
                style: {order: -1},
                gameInfoStyle: {display: "block"}
            });
            window.scrollTo({top: 0, behavior: 'smooth'});
        }          
    }
    render() {
        return (
            <div className={this.state.cssClass} style= {this.state.style}>
                <h1>Paused</h1>
                <div className="divTwo">
                    <TwoCanvas
                        project={this.props.project.project}
                        key={this.props.project}
                        width={this.state.width}
                        height={this.state.height}
                        type={this.props.project.project == PathFinder ? Two.Types.webgl : Two.Types.canvas}
                    />
                    <button onClick={() => this.maximize()}></button>
                </div>
                <h2 className="sim-title" style= {this.state.style}>{this.props.project.title}</h2>
                <div className="game-info" style={this.state.gameInfoStyle}>
                    <p className="sim-desc">{this.props.project.description}</p>
                </div>
                
            </div>
        );
    }
}

export default ProjectCard;